@import './core/_reset.scss';
@import './core/_mixins.scss';
@import './core/_variables.scss';

.page {
  background-color: $color-background;
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  flex-wrap: wrap;

  @include tablet {
    grid-template-rows: 300px 1fr;
  }

  @include desktop {
    grid-template-rows: 200px 1fr;
  }
}
