@import '../core/_mixins.scss';
@import '../core/_variables.scss';

.link {
  text-decoration: none;
}

.character {
  box-sizing: border-box;
  display: flex;

  border-radius: 25px;
  box-shadow: 5px 5px 10px $color-icon;
  border: 3px solid $color-detail;

  &:hover {
    transform: scale(1.05);
  }

  &__card {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 5rem 2rem 12rem 5rem;

    &--img {
      grid-row: 3/4;
      width: 100%;
    }

    &--name {
      grid-row: 1/2;
      margin: 20px 25px 0 25px;
      color: $color-detail;
      padding: 3px 0 5px 0;
      background-color: $color-background;
      border-radius: 5px;
      font-family: $font-title;
      font-size: 20px;
    }

    &--info {
      font-family: $font-main;
      grid-row: 4/5;
      margin-top: 15px;
      color: $color-icon;
      text-transform: capitalize;
      background-color: $color-background;
      border-radius: 40%;

      &-span {
        background-color: $color-background;
      }
    }
  }
}
