@import '../core/_mixins.scss';
@import '../core/_variables.scss';

.footer {
  background-color: $color-background;
  width: 100%;
  height: 110px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid $color-detail;
  margin-top: 20px;
  bottom: 0;

  &__small{
      color: $color-icon;
      font-family: $font-main;
      margin: 20px;
  }

  &__logo {
    height: 40px;
    margin: 20px;
  }
}
